import { useEffect } from 'react';
import { FieldValues, Path, UseFormProps, useForm } from 'react-hook-form';

import { InputState } from '#components/Form/Input/Input.types';

import { errorId, hintId, resolver } from '#utils/formValidation';
import { Schema } from '#utils/schema/';
import { ValidationErrors } from '#utils/schema/schema.types';

/**
 * This builds on top of the react-hook-form library
 * to provide a consistent handling of form state
 *
 * Designed to be used with TextInput & PasswordInput
 * and could be extended to support other input types
 *
 */
const useIndyForm = <T extends FieldValues>(
  schema: Schema<T>,
  options: UseFormProps<T> = {},
) => {
  if (!schema) {
    throw new Error(
      'No schema provided! Please provide a schema to useIndyForm',
    );
  }

  const methods = useForm<T>({
    mode: 'onTouched',
    resolver: resolver<T>(schema as Schema<T>),
    ...options,
  });

  const { formState, getValues, setError } = methods;

  useEffect(() => {
    const schemaFields = schema.fields;
    const formFields = Object.keys(getValues());

    const missingFields = schemaFields.filter(
      (field) => !formFields.includes(field as string),
    );

    if (missingFields.length > 0) {
      throw new Error(
        `Fields ["${missingFields.join(
          '", "',
        )}"] are included in the schema but missing or inaccessible by React Hook Form`,
      );
    }
  }, [getValues, schema.fields]);

  const getTextInputError = (id: keyof T) => {
    const formErrors = formState.errors as ValidationErrors<T>;
    const textInputErrors = formErrors[id];

    if (textInputErrors) {
      return textInputErrors[0].messageBuilder(schema.names[id]);
    }

    return undefined;
  };

  const getTextInputState = (id: keyof T) => {
    const formErrored = !!formState.errors['root'];
    const fieldIsErrored = !!formState.errors[id];
    const fieldHasErrored =
      formState.dirtyFields[id as string] && !fieldIsErrored;

    if (fieldIsErrored || formErrored) {
      return InputState.ERROR;
    }

    if (fieldHasErrored) {
      return InputState.SUCCESS;
    }

    return InputState.DEFAULT;
  };

  const registerControlledInput = (id: keyof T) => {
    const fieldIsRequired = schema.get(id)?.isRequired || false;
    const fieldError = getTextInputError(id);
    const fieldState = getTextInputState(id);

    return {
      id: id as keyof T,
      state: fieldState,
      message: fieldError,
      required: fieldIsRequired,
      'aria-required': fieldIsRequired,
      'aria-invalid': fieldError === 'error',
      // Feb 2024 - errors should be announced by aria-errormessage in the future,
      // but it is currently not well supported sccross screen readers/browsers
      'aria-describedby': `${errorId(id)} ${hintId(id)}`,
    };
  };

  const setFormError = (message: string) => {
    setError('root', { message });
  };

  const setFieldError = (name: Path<T>, message: string) => {
    setError(name, { message }, { shouldFocus: true });
  };

  const getFormError = () => getTextInputError('root');
  const getFieldErrors = () =>
    schema.fields.map(getTextInputError).filter(Boolean) as string[];
  return {
    registerControlledInput,
    methods: {
      ...methods,
      setFormError,
      setFieldError,
      getFormError,
      getFieldErrors,
    },
    formState: {
      ...formState,
      errors: formState.errors as ValidationErrors<T>,
    },
  };
};

export default useIndyForm;
