import { capitalise, count, parseString } from '#utils/formValidation';

import {
  CharacterType,
  SchemaFn,
  SchemaType,
  UnknownValues,
  ValidationError,
  ValidationErrorConfig,
} from './schema.types';

/*
 * These functions are used to create a schema for a form
 * They are used in the SchemaEntry class to create a chainable
 * schema format, and then accessed in the Schema class to validate
 * form values
 */

/**
 * Return an error if statement is untrue, otherwise return null
 *
 * @param type error type
 * @param statement boolean statement
 * @param message error message to show to user
 * @returns
 */
const handler = ({
  type,
  condition,
  message,
  messageBuilder,
}: ValidationErrorConfig): ValidationError | null => {
  if (condition) {
    return null;
  }

  return {
    type,
    message,
    messageBuilder,
  };
};

export const isNotEmpty: SchemaFn = () => ({
  validate: (value: unknown) => {
    const parsedValue = parseString(value) as string;
    const isNotEmpty = (parsedValue as string).length > 0;

    return handler({
      type: 'required',
      condition: isNotEmpty,
      message: 'Field is required',
      messageBuilder: (fieldName: string) => `${fieldName} is required`,
    });
  },
});

// primitive types
export const isString: SchemaFn = () => ({
  validate: (value: unknown) => {
    const parsedValue = parseString(value);
    const isString = typeof parsedValue === 'string';

    return handler({
      type: 'isString',
      condition: isString,
      message: 'Field must be text only',
      messageBuilder: (fieldName: string) => `${fieldName} must be text only`,
    });
  },
});

export const isNumber: SchemaFn = () => ({
  validate: (value: unknown) => {
    const isNumeric = !isNaN(value as number);

    return handler({
      type: 'isNumber',
      condition: isNumeric,
      message: 'Field must be a number',
      messageBuilder: (fieldName: string) => `${fieldName} must be a number`,
    });
  },
});

/**
 * Value must be a valid email address
 * @param config
 */
export const isEmail: SchemaFn = () => ({
  validate: (value: unknown) => {
    const parsedValue = parseString(value) as string;
    const regExp = new RegExp(/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i);
    const isEmail = regExp.test(parsedValue);

    return handler({
      type: 'isEmail',
      condition: isEmail,
      message: 'Please enter a valid email address',
      messageBuilder: () => `Please enter a valid email address`,
    });
  },
});

/**
 * Value must match to value of another field
 * @param config
 */
export const doesMatch = (fieldName: string): SchemaType => ({
  validate: (value: unknown, values: UnknownValues) => {
    const parsedValue = parseString(value) as string;
    const isMatchingField = parsedValue === values[fieldName];

    return handler({
      type: 'doesMatch',
      condition: isMatchingField,
      message: 'Fields must match',
      messageBuilder: (fieldName: string) => `${fieldName} fields must match`,
    });
  },
});

/**
 * Value must have length of any/particular kind of character
 * @param config
 */
export const hasMin = (
  minLen: number,
  countType?: CharacterType,
): SchemaType => ({
  validate: (value: unknown) => {
    const parsedValue = parseString(value) as string;
    const len = count(parsedValue as string, countType);

    const defaults = {
      type: `hasMin${capitalise(countType || '')}`,
      condition: len >= minLen,
    };

    if (countType) {
      return handler({
        ...defaults,
        message: `Must contain minimum ${minLen} ${countType}`,
        messageBuilder: () => `Must contain minimum ${minLen} ${countType}`,
      });
    }

    return handler({
      ...defaults,
      message: `Minimum length is ${minLen} characters`,
      messageBuilder: () => `Minimum length is ${minLen} characters`,
    });
  },
});

/**
 * Value must not exceed length of any/particular kind of character
 * @param config
 */
export const hasMax = (
  maxLength: number,
  countType?: CharacterType,
): SchemaType => ({
  validate: (value: unknown) => {
    const parsedValue = parseString(value) as string;
    const length = count(parsedValue as string, countType);

    const defaults = {
      type: `hasMax${capitalise(countType || '')}`,
      condition: length <= maxLength,
    };

    if (countType) {
      return handler({
        ...defaults,
        message: `Must contain maximum ${maxLength} ${countType}`,
        messageBuilder: () => `Must contain maximum ${maxLength} ${countType}`,
      });
    }

    return handler({
      ...defaults,
      message: `Maximum length is ${maxLength} characters`,
      messageBuilder: () => `Maximum length is ${maxLength} characters`,
    });
  },
});

/**
 * Value must be within a range of length of any/particular kind of character
 * @param config
 */
export const hasRange = (
  minLength: number,
  maxLength: number,
  countType?: CharacterType,
): SchemaType => ({
  validate: (value: unknown) => {
    const parsedValue = Number(value);

    const defaults = {
      type: `hasRange${capitalise(countType || '')}`,
      condition: parsedValue >= minLength && parsedValue <= maxLength,
    };

    if (countType) {
      return handler({
        ...defaults,
        message: `Must contain a minimum value of ${minLength} ${countType} and a maximum value of ${maxLength} ${countType}`,
        messageBuilder: () =>
          `Must contain a minimum value of ${minLength} ${countType} and a maximum value of ${maxLength} ${countType}`,
      });
    }

    return handler({
      ...defaults,
      message: `Minimum value is ${minLength} characters and maximum value is ${maxLength} characters`,
      messageBuilder: () =>
        `Minimum value is ${minLength} characters and maximum value is ${maxLength} characters`,
    });
  },
});

/**
 * Value must not contain any spaces
 * @param config
 */
export const containsNoSpaces = (): SchemaType => ({
  validate: (value: unknown) => {
    const parsedValue = parseString(value) as string;

    const defaults = {
      type: 'containsNoSpaces',
      condition: !parsedValue.includes(' '),
    };

    return handler({
      ...defaults,
      message: 'Must not contain any spaces',
      messageBuilder: () => 'Must not contain any spaces',
    });
  },
});

/**
 * Value must match a specific regular expression pattern
 * @param regex
 * @param config
 */
export const matchesRegex = (regex: RegExp): SchemaType => ({
  validate: (value: unknown) => {
    const parsedValue = parseString(value) as string;

    const defaults = {
      type: 'matchesRegex',
      condition: regex.test(parsedValue),
    };

    return handler({
      ...defaults,
      message: 'Field does not match the required format',
      messageBuilder: () => 'Field does not match the required format',
    });
  },
});
