import SchemaEntry from '#utils/schema/schemaEntry';

import { ValidationError, ValidationErrors, Validator } from './schema.types';
import { isNotEmpty } from './schemaFunctions';

/**
 * Schema class made up of SchemaEntry objects, creating a
 * chainable schema format for validating data
 */
class Schema<T> {
  private self: Record<keyof T, SchemaEntry>;
  fields: (keyof T)[];
  names: Record<keyof T, string>;
  errors: ValidationErrors<T> = {} as ValidationErrors<T>;

  constructor(schema: Record<keyof T, SchemaEntry>) {
    this.self = schema;
    this.fields = Object.keys(schema) as (keyof T)[];
    this.names = Object.entries(schema).reduce(
      (acc, [key, value]) => ({
        ...acc,
        [key]: (value as SchemaEntry).name,
      }),
      {} as Record<keyof T, string>,
    );

    return this;
  }

  /**
   * Add a schema entry to the schema
   * @param name human readable field name to be used in messaging
   * @returns a new SchemaEntry object
   */
  static add(name: string) {
    return new SchemaEntry(name);
  }

  /**
   * @param field field ID/key
   * @returns SchemaEntry for the given field
   */
  get(field: keyof T) {
    return this.self[field];
  }

  /**
   * @param field field ID/key
   * @returns SchemaEntry for the given field
   */
  getErrors(field: keyof T) {
    return this.errors[field as keyof T];
  }

  /**
   * @param field field ID/key
   * @param errors  validation errors to set for the given field
   */
  setErrors(field: keyof T, errors: ValidationError[] | null) {
    if (!errors || errors.length === 0) {
      // remove key from errors object so this can
      // be easily evaluated as falsey
      delete this.errors[field as keyof T];
    } else {
      this.errors[field as keyof T] = errors;
    }
  }

  /**
   * Validate a value against the schema for a singular field
   *
   * @param key field ID/key
   * @param value field value
   * @param values an object containing all field vales, required for some validations (e.g. isMatch)
   * @returns an array of errors for the given field
   */
  validate(field: keyof T, value: unknown, values: Record<string, unknown>) {
    const entry = this.get(field);

    if (!entry) {
      return;
    }

    // if a field is required but empty we should prioritise that error over
    // any other errors
    const isRequired = entry.isRequired;
    const fieldEmptyError = isNotEmpty().validate(value, values);

    if (isRequired && fieldEmptyError) {
      this.setErrors(field, [fieldEmptyError]);
      return this.getErrors(field as keyof T);
    }

    // field has values so continue with remaining validation
    const freshErrors = entry.validators.reduce(
      (acc: ValidationError[], validatorCreator: Validator) => {
        const [creator, args] = validatorCreator;

        const validator = creator(...args);
        const error = validator.validate(value, values);

        if (error) {
          return [...acc, error];
        }

        return acc;
      },
      [] as ValidationError[],
    );

    this.setErrors(field, freshErrors);
    return this.getErrors(field as keyof T);
  }
}
export default Schema;
