import React from 'react';
import { SubmitHandler } from 'react-hook-form';

import Button from '#components/Button';
import {
  Form,
  FormProvider,
  TextInput,
  PasswordInput,
  SelectInput,
  Schema,
} from '#components/Form';

import { Wrapper } from './TestableForm.styles';

import useIndyForm from '#hooks/useIndyForm';

type ExampleFormData = {
  fullName: string;
  email: string;
  password: string;
  birthYear: string;
};

type TestableFormProps = { onSubmit: SubmitHandler<ExampleFormData> };

export const schema = new Schema<ExampleFormData>({
  fullName: Schema.add('Full name').string(),
  email: Schema.add('Email address').string().isEmail().required(),
  password: Schema.add('Password')
    .string()
    .hasMin(1, 'digits')
    .hasMin(6, 'letters')
    .required(),
  birthYear: Schema.add('Birth year').number().required(),
});

/**
 * This form exists to demonstrate how to use the useIndyForm hook
 * alongside the Form, TextInput and PasswordInput components
 *
 * It us also used for testing functionality in a form context
 * with full validation and error handling, and providing a
 * component in storybook for visual testing
 */
const TestableForm: React.FC<TestableFormProps> = ({ onSubmit }) => {
  const { methods, registerControlledInput } = useIndyForm(schema);
  const { handleSubmit, formState } = methods;

  return (
    <Wrapper>
      <FormProvider {...methods}>
        <Form>
          <TextInput label="Name" {...registerControlledInput('fullName')} />
          <TextInput
            type="email"
            label="Email address"
            helpText="Add an email address"
            {...registerControlledInput('email')}
          />
          <PasswordInput
            label="Password"
            helpText="Make it a good one"
            {...registerControlledInput('password')}
          />
          <SelectInput
            label="Birth year"
            options={[
              { value: '1970', label: '1970' },
              { value: '1980', label: '1980' },
              { value: '1990', label: '1990' },
            ]}
            {...registerControlledInput('birthYear')}
          ></SelectInput>
          <Button
            type="submit"
            disabled={!formState.isValid}
            onClick={handleSubmit(onSubmit)}
          >
            Sign up
          </Button>
        </Form>
      </FormProvider>
    </Wrapper>
  );
};

export default TestableForm;
