import { UnknownFn, SchemaFn, Validator } from './schema.types';
import {
  doesMatch,
  hasMin,
  hasMax,
  isEmail,
  isNumber,
  isString,
  hasRange,
  containsNoSpaces,
  matchesRegex,
} from './schemaFunctions';

const NO_PARAMS = [] as unknown[];

/**
 * SchemaEntry class designed to be used with the Schema class to
 * create a chainable schema format for validating flat data e.g. new Schema().string().isEmail(0)
 */
class SchemaEntry {
  name: string;
  isRequired?: boolean = false;
  validators: Validator[] = [];

  /**
   * @param name name of the field to be used in messaging
   */
  constructor(name: string) {
    this.name = name;
  }

  private initialise(fns: Validator[]) {
    this.validators = fns;
  }

  private checkValid() {
    if (!this.validators.length) {
      throw new Error('No validators set');
    }
  }

  // primitives
  string(): SchemaEntry {
    this.initialise([[isString, NO_PARAMS]]);
    return this;
  }

  number(): SchemaEntry {
    this.initialise([[isNumber, NO_PARAMS]]);
    return this;
  }

  // special
  required(): SchemaEntry {
    this.isRequired = true;

    return this;
  }

  // conditions

  /**
   * A check to validate field value is an email
   *
   * @returns SchemaEntry
   */
  isEmail(...args: Parameters<SchemaFn>): SchemaEntry {
    this.checkValid();

    this.validators.push([isEmail as UnknownFn, args]);
    return this;
  }

  /**
   * A check to validate field value matches another field's value
   *
   * @param fieldName
   * @returns SchemaEntry
   */
  doesMatch(...args: Parameters<typeof doesMatch>): SchemaEntry {
    this.checkValid();

    this.validators.push([doesMatch as UnknownFn, args]);
    return this;
  }

  /**
   * A check to validate field has a minimum length or minimum
   * count of letters/digits
   *
   * @param minLength
   * @param countType
   * @returns SchemaEntry
   */
  hasMin(...params: Parameters<typeof hasMin>): SchemaEntry {
    this.checkValid();

    this.validators.push([hasMin as UnknownFn, params]);
    return this;
  }

  /**
   * A check to validate field has a maximum length or maximum
   * count of letters/digits
   *
   * @param minLength
   * @param countType
   * @returns SchemaEntry
   */
  hasMax(...params: Parameters<typeof hasMax>): SchemaEntry {
    this.checkValid();

    this.validators.push([hasMax as UnknownFn, params]);
    return this;
  }

  /**
   * A check to validate field has a range of length or count of
   * letters/digits
   *
   * @param minLength
   * @param maxLength
   * @param countType
   * @returns SchemaEntry
   */
  hasRange(...params: Parameters<typeof hasRange>): SchemaEntry {
    this.checkValid();

    this.validators.push([hasRange as UnknownFn, params]);
    return this;
  }

  /**
   * A check to validate field contains no spaces
   * @returns SchemaEntry
   */
  containsNoSpaces(
    ...params: Parameters<typeof containsNoSpaces>
  ): SchemaEntry {
    this.checkValid();

    this.validators.push([containsNoSpaces as UnknownFn, params]);
    return this;
  }

  /**
   * A check to validate field matches the required format
   * @returns SchemaEntry
   */
  matchesRegex(...params: Parameters<typeof matchesRegex>): SchemaEntry {
    this.checkValid();

    this.validators.push([matchesRegex as UnknownFn, params]);
    return this;
  }
}

export default SchemaEntry;
